<template>
  <div class="home">
    <!--  -->
    <div class="wrap-top">
      <!-- <van-nav-bar :border="false" left-arrow @click-left="onClickLeft" /> -->
      <div class="tip-title" v-if="recordStatus === 1 || recordStatus === 3"></div>
      <!-- <div class="tip-title" v-if="recordStatus === 2"> {{ titles }}</div> -->
      <div class="tip-title" v-if="recordStatus === 2"></div>
      <div class="tip-title" v-if="recordStatus === 4">...</div>

      <!--  -->
      <cw-video :showDefaultImg="!isOpenVideo" :loading="loading" ref="cwVideoRef">
        <template #imgSlot>
          <div class="video-wrap" id="video-box" v-show="isOpenVideo">
            <video id="videoPlay" playinline autoplay></video>
          </div>
        </template>
      </cw-video>
    </div>
    <div class="wrap-bottom">
      <!--  -->
      <TipInfo v-if="isShowInfo" />
      <!--  -->
      <div class="op-btn" v-if="isShowBtn">
        <CwButton @click="openCamera" :loading="btnLoading"></CwButton>
      </div>
    </div>

    <!--  -->
    <CwNativeCameraPopup v-model="showNativeCamera" :titles="titles" @nativeRecord="nativeRecord"
      @cancelClick="nativeCancelClick" />
    <CwRsultPopup v-model="showResult" @reRecord="reRecord" @cancelClick="cancelClick" />
  </div>
</template>

<script>
import CwButton from '@/components/cw-button/index.vue'
import CwVideo from '@/components/cw-video/index.vue'
import CwNativeCameraPopup from '@/components/cw-native-camera-popup/index.vue'
import CwRsultPopup from '@/components/cw-result-popup/index.vue'
import axios from 'axios'
import TipInfo from './components/tip-info.vue'
const URL = window.config.uploadUrl
const ACTION_NUM = window.config.actionNum || 2 // 

export default {
  components: {
    CwButton,
    CwVideo,
    CwNativeCameraPopup,
    CwRsultPopup,
    TipInfo
  },
  data() {
    return {
      showNativeCamera: false,
      showResult: false,
      //  cwVideoRef: false,
      videoInstance: null,
      isSupport: true,
      btnLoading: false,
      recordStatus: 1,
      activeActions: [],
      isUploadFinish: false,
      actionsArr: [
        { title: '', action: '1' },
        { title: '', action: '2' },
        { title: '', action: '5' }
      ]
    }
  },
  computed: {
    isOpenVideo() {
      return this.isSupport && this.recordStatus === 2
    },
    isShowInfo() {
      return this.recordStatus === 1 || this.recordStatus === 3
    },
    isShowBtn() {
      return this.recordStatus === 1 || this.recordStatus === 3
    },
    loading() {
      return this.recordStatus === 4 && !this.isUploadFinish
    },
    titles() {
      const titlesArr = this.activeActions.map(ele => ele.title)
      return titlesArr.join('')
    },
    actions() {
      const actionsArr = this.activeActions.map(ele => ele.action)
      return actionsArr.join(',')
    }
  },
  mounted() {
    this.initFs()
    this.initVideo()
  },
  methods: {
    // 
    initFs() {
      if (ACTION_NUM === 1) {
        const num = Math.floor(Math.random() * (this.actionsArr.length - 1) + 1)
        this.activeActions = this.actionsArr.filter((ele, index) => index === num - 1)
      } else if (ACTION_NUM === 2) {
        const num = Math.floor(Math.random() * (this.actionsArr.length - 1) + 1)
        this.activeActions = this.actionsArr.filter((ele, index) => index !== num - 1)
      } else {
        this.activeActions = [...this.actionsArr]
      }
    },

    // cameara
    openCamera() {
      const self = this
      if (self.isSupport) {
        self.btnLoading = true
        // 
        // 
        self.videoInstance.openCamera({
          onError() {
            console.warn('')
            self.showNativeCamera = true
            self.isSupport = false
            self.recordStatus = 3
            self.btnLoading = false
          },
          onSuccess() {
            console.warn('')
            self.recordStatus = 2
            self.startCollect()
            self.btnLoading = false
          }
        })
      } else {
        // 
        self.showNativeCamera = true
      }
    },

    // 
    startCollect() {
      const recordTime = (window.config.recordTime || 5000) / 1000
      let isFirst = true
      const self = this
      self.videoInstance.startCollect({
        time: recordTime, // , 
        timeCallback(clock) {
          if (isFirst) {
            isFirst = false
            self.$refs.cwVideoRef.start(recordTime)
          }
          console.warn(clock)
          if (clock <= 0) {
            self.recordStatus = 4
          }
        }
      })
    },

    // 
    initVideo() {
      this.recordStatus = 1
      const self = this
      // 
      this.videoInstance = new CWMediaCollector({
        webmWasmPath: './static/webm-wasm.wasm', // 
        webmWorkerPath: './static/webm-worker.js', // webm-worker.js
        el: document.getElementById('video-box'), // document.getElementById('video-box'), // , el
        height: 480,
        width: 480,
        autoOpenNativeCamera: false, // 
        mode: 1, // mode, // 123
        maxFileSize: 10, // 10, // (M)
        frameRate: 10,
        isFrontCamera: true,
        mimeType: 'video/webm;codecs=h264',
        onInitSuccess(res) {
          if (res.code === 20002) {
            self.isSupport = false
            // 
          } else {
            self.isSupport = true
          }
        },
        onSuccess: function (res) {
          // 
          console.log('recordingEnd==', res)
          if (res.code === 20000) {
            console.log('')
            if (res.data) {
              self.postBlob(res.data)
              console.warn('', res.data)
              self.recordStatus = 4
              self.showNativeCamera = false
            } else {
              console.warn('0')
              self.showResult = true
            }
          } else {
            // 
            self.showResult = true
          }
        },
        onError: function (res) {
          console.log(' ~ html ~ initVideo ~ res', res, res.code, '--', res.message)
          console.log(res.message)
          // 
          self.showResult = true
        }
      })
    },
    // 
    setResult(result) {
      localStorage.setItem('result', JSON.stringify(result))
    },
    // 
    postBlob(blob) {
      const self = this
      CWMediaCollector.blobToBase64(blob, function (base64Data) {
        console.warn('', base64Data)
        console.warn('Byte', base64Data.length)
        self.checkFace(base64Data, function (res) {
          if (res.isAlive && res.image) {
            const MiniProgramUrl = window.location.href
            console.log('MiniProgramUrl', MiniProgramUrl)
            if (MiniProgramUrl.indexOf('?') === -1) {
              self.setResult({
                result: true,
                size: base64Data.length,
                image: `data:image/jpeg;base64,${res.image}`,
                res,
                mode: 64
              })
              self.$router.push({
                path: '/result'
              })
            }
            const queryString = MiniProgramUrl.substring(MiniProgramUrl.indexOf('?') + 1)
            console.log('queryString', queryString)
            const params = new URLSearchParams(queryString)
            console.log('params', params)
            const mode = params.get('mode').substr(0, params.get('mode').length - 2)
            console.log('mode', mode)

            self.setResult({
              result: true,
              size: base64Data.length,
              image: `data:image/jpeg;base64,${res.image}`,
              res,
              mode
            })
            self.$router.push({
              path: '/result'
            })
          } else {
            self.showResult = true
          }
        })
      })
    },
    // face
    checkFace(videoData, callback) {
      // 
      const data = {
        ...(window.config.uploadParameter || {}),
        video: videoData,
        // action: this.actions
        action: 0
      }
      this.isUploadFinish = false
      axios
        .post(URL, data)
        .then(res => {
          if (res.status === 200) {
            callback(res.data)
          } else {
            console.warn('status !== 200:', res.status)
          }
        })
        .catch(err => {
          console.warn(':', err)
        })
        .finally(() => {
          this.isUploadFinish = true
        })
    },
    // 
    reRecord() {
      console.warn('')
      this.showResult = false
      this.$refs.cwVideoRef.init() // 
      this.initFs()
      this.initVideo()
    },
    // 
    nativeRecord() {
      this.videoInstance && this.videoInstance.openNativeCamera()
    },
    // 
    onClickLeft() {
      console.warn('')
      const url = window.config.returnUrl
      location.href = url
      // 
    },

    // 
    cancelClick() {
      console.warn('')
      // 
      const isAlipay = navigator.userAgent.indexOf('AliApp') > -1 // 
      let globalEnv
      try {
        // eslint-disable-next-line no-undef
        globalEnv = isAlipay ? my : wx.miniProgram // h5 with 
      } catch (error) {
        console.log(error)
      }
      globalEnv.navigateBack({ delta: 1 })
    },
    // 
    nativeCancelClick() {
      const url = window.config.returnUrl
      location.href = url
    }
  }
}
</script>

<style lang="less" scoped>
.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;

  .op-btn {
    padding: 0 28px;
  }

  .tip-title {
    font-size: 36px;
    color: #333742;
    text-align: center;
    line-height: 36px;
    font-weight: 400;
    padding-top: 120px;
    padding-bottom: 60px;
  }

  .wrap-bottom {
    padding-bottom: 120px;
    padding-top: 60px;
  }
}
</style>
